<template>
    <section>
        <el-divider></el-divider>
        <router-link :to="{ name: '模型版本详情', params: {
                          model_name:this.$route.params.model_name,
                          version_id:this.$route.params.version_id}}" 
                     style='text-decoration: none;color: aliceblue;'>
            <el-button class="return-list"><i class="el-icon-d-arrow-left" style="margin-right: 5px"></i>模型版本详情</el-button>
        </router-link>
        <router-link :to="{ name: '模型版本详情', params: {
                          model_name:this.$route.params.model_name,
                          version_id:this.$route.params.version_id}}" style='text-decoration: none;color: aliceblue;'>
            <el-button class="return-list" style="float: right">取消</el-button>
        </router-link>
<br><br>
<template>
    <el-card class="box-card">
        <textarea id = "div1"  style="width:1650px; height:740px;display:none" readonly>
        </textarea>
        <pre class="code">{{file_content}}</pre>
        <div v-show="!file_content" class="raw">暂无数据！</div>
    </el-card>
</template>
</section>
</template>
<script>
import { test } from '@/api/api'
import $ from 'jquery'
export default {
  data () {
    return {
      file_content: '',
      listLoading: false
    }
  },
  methods: {
      getFileContent () {
          let self = this
          let param = JSON.stringify({ model_name: this.$route.params.model_name,
                                      filepath: this.$route.params.filepath,
                                      fileToView: this.$route.params.fileToView,
                                      version_id: this.$route.params.version_id })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'POST',
        url: test + '/api/models/cat_file_contents',
        async: true,

        data: param,

        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
          document.getElementById('div1').innerHTML = '读取中...'
          self.file_content = data
          // $("#div1").html(this._code_content2);
        }
      })
    }
  },
  mounted () {
    this.getFileContent()
    document.getElementById('div1').innerHTML = '读取中...'
    $(document).ready(function () {
      $('#div1').html(this.file_content)
    })

    console.log(this.$route.params, 'dldldl')
  }
}
</script>

